// Organisasjonsside-menu
// ---------------------------

// The Container
.org_menu_container {
    @include rem('border', 1px solid $zombie);
    border-top: 0 none;
    @include border-radius(0 0 $base_border_radius $base_border_radius);
    @include rem-box-shadow(0 1px 2px $alto);
    overflow: hidden;
    
    @media (min-width: $lg_break) {
        overflow: visible;
        border: 0 none;
        @include box-shadow(none);
    }
    
    a:focus {
        outline-offset: -3px;
    }

    // Forhåndsvisning - deaktiverer hele menyen
    &--inactive {
        position: relative;
        background-color: $white;
        opacity: .6;

        a {
            pointer-events: none;
            user-select: none;
        }
    }
}

// The mobile toggle link
.org_menu_mobile_header {
    display: block;
    color: $link_color;
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    background-color: $rum_swizzle;
    @include rem('padding', 13px 50px 14px 10px);
    position: relative;
    
    &:hover,
    &:active {
        color: $link_color_hover;
        text-decoration: underline;
    }

    .arrow-icon {
        @include single-arrow-icon($direction: 'down', $size: 'large');
        position: absolute;
        @include rem('top', 27px);
        @include rem('right', 14px);
    }

    .org_menu_expanded & {
        @include rem('border-bottom', 1px solid $almond);
        @include rem-box-shadow(0 1px 2px $alto);
        
        .arrow-icon {
            @include single-arrow-icon($direction: 'up', $size: 'large');
            position: absolute;
            @include rem('top', 27px);
            @include rem('right', 14px);
        }
    }

    @media (min-width: $lg_break) {
        display: none;
    }
}

// The Menu
.org_menu {
    @include hyphens();
    padding: 0;
    margin: 0;
    @include transition($property: opacity);
    
    li {
        position: relative;
        z-index: 0;
        list-style: none;
		@include rem('border-bottom', 1px solid $almond);
        
        .org_menu_link {
            display: block;
            @include rem('padding', 10px 0 10px 10px);

            &:hover,
            &:active {
                background-color: $rum_swizzle;
                text-decoration: none;
            }
        }
        
        &.active > a {
            @include SourceSansProBold();
        }
    }
    
    // Toggle Effect
    .org_menu_collapsed & {
        height: 0;
        opacity: 0;
    }
    
    // Reset mobile styling on desktop
    @media (min-width: $lg_break) {
        .org_menu_collapsed & {
            height: auto;
            opacity: 1;
        }
    }
}


// The sub menu
li.with_org_sub_menu,
.org_menu--editable li {

    .org_menu_link {
        @include rem('padding-right', 10px);
        @include rem('margin-right', 48px);
        @include rem('border-right', 1px solid $almond);
    }
    
    ul {
        position: relative;
        z-index: 2;
        padding: 0;
        margin: 0;
        background-color: $rum_swizzle;
        @include rem('border-top', 2px solid $almond);
        @include rem('border-bottom', 1px solid $almond);
        
        li {
            @include rem('margin-left', 25px);
            @include rem('margin-right', 25px);
            border-bottom-color: $white;
            
            .org_sub_menu_link {
                display: block;
                @include rem('padding', 11px 12px 9px 29px);
                @include rem('margin-left', -25px);
                @include rem('margin-right', -25px);

                &:hover,
                &:active {
                    background-color: darken($rum_swizzle, 3);
                    text-decoration: none;
                }
            }
        }

        li:last-child {
            border-bottom: 0 none;
        }
    }
    
    @media (min-width: $lg_break) {
        ul li .org_sub_menu_link {
            @include rem('padding-left', 25px);
        }
    }
}

// Toggle sub menu button
.org_sub_menu_toggle_link {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    @include rem('height', 47px);
    @include rem('width', 48px);
    background-color: $white;

    &:hover,
    &:active {
        background-color: $rum_swizzle;
    }

    .arrow-icon {
        @include single-arrow-icon($direction: 'down', $size: 'medium');
        position: absolute;
        @include rem('top', 25px);
        @include rem('right', 17px);
    
        .org_sub_menu_expanded & {
            @include single-arrow-icon($direction: 'up', $size: 'medium');
            position: absolute;
            @include rem('top', 25px);
            @include rem('right', 17px);
        }
    }
    
    @media (min-width: $lg_break) {
        .arrow-icon {
            @include rem('top', 23px !important);
        }
    }
}
   
// The sub menu - collapsed / expanded
.org_sub_menu_collapsed ul {
    display: none;
}
.org_sub_menu_expanded {
    ul {
        display: block;
    }
}


// Redigering av menyen
// -------------------------------------
.org_menu--editable li {
    
    .org_menu_link {
        margin-right: 0;
        border-right: 0 none;
    }
    
    .org_menu--editable__flex-container {
        @include flexbox();
        @include flex-flow($values: (row nowrap));
        width: 100%;

        .org_menu_link {
            @include rem('min-width', 150px);
            @include calc(max-width, "100% - 7.125rem"); // 7.125rem = 3 ikon-links á 38px
            flex-basis: auto;
            flex-grow: 1;
        }

        .edit-menu__edit-button {
            @include rem('height', 47px);
            @include rem('width', 38px);

            .grunt-icon {
                @include rem('margin', 7px auto 0);
            }

            &:hover {
                background-color: $rum_swizzle;
            }
        }
        
        .org_sub_menu_toggle_link {
            position: relative;
            @include rem('width', 38px);
            
            // Default arrow icons
            .arrow-icon {
                @include rem('right', 12px);
            }
            
            // Toggle ikon for menypunkt uten undermeny
            .plus-arrow-icon {
                position: absolute;
                @include rem('top', 25px);
                @include rem('right', 12px);
                @include plus-icon();
            }
        }
    }
}


// Expanded toggle ikon for menypunkt uten undermeny
.org_menu--editable li.org_sub_menu_expanded .org_sub_menu_toggle_link .plus-arrow-icon {
    @include single-arrow-icon($direction: 'up', $size: 'medium');
    position: absolute;
    @include rem('top', 25px);
    @include rem('right', 12px);
}


// Submenu
.org_menu--editable li ul li {
    margin-left: 0;
    margin-right: 0;

    .org_sub_menu_link {
        margin-left: 0;
        margin-right: 0;
        padding-left: 0;
        padding-right: 0;
    }

    .org_menu--editable__flex-container {

        .org_sub_menu_link {
            @include rem('padding-left', 25px);
            @include rem('min-width', 150px);
            @include calc(max-width, "100% - 4.75rem"); // 4.75rem = 2 ikon-links á 38px
            flex-basis: auto;
            flex-grow: 1;
        }

        .edit-menu__edit-button:hover {
            background-color: darken($rum_swizzle, 3);
        }
    }
}


// Input for new menu item
.org_menu--editable .form-element {
    position: relative;

    &__textfield {
        @include rem('padding', 10px 6px);
        @include calc(width, "100% - 2.375rem"); // 2.375rem = 38px
        border: 0 none;
        @include rem('border-left', 1px solid $almond);
        @include border-radius(0);

        &:focus {
            outline-offset: -3px;
        }
    }

    button {
        position: absolute;
        @include rem('top', 0);
        @include rem('right', 0);
        @include rem('height', 47px);
        @include rem('width', 38px);
        padding: 0;
        @include border-radius(0);

        .grunt-icon {
            margin: 0 auto;
        }

        &:focus {
            outline-offset: -3px;
        }
    }
}
// Submenu
.org_menu--editable ul .form-element .form-element__textfield {
    @include rem('margin-left', 14px);
    @include rem('padding', 11px 6px 9px 6px);
    @include calc(width, "100% - 2.375rem - .875rem"); // 2.375rem = 38px // .875rem = 14px
}


// Toggle new input link
.org_menu--editable li .add_item_link {
    position: relative;
    @include rem('padding-left', 38px);

    // Nytt menypunkt ikon
    .plus-icon {
        position: absolute;
        @include rem('top', 23px);
        @include rem('left', 11px);
        @include plus-icon();
    }
    
    // Submenu
    &.org_sub_menu_link {
        // Add 15px space from left
        @include rem('padding-left', 50px);

        .plus-icon {
            @include rem('left', 25px);
        }
    }
}

// Active
.org_menu--editable li.active > .org_menu--editable__flex-container > a {
    @include SourceSansProBold();
}





